<template>
  <div class="app">
    <el-carousel class="banner" :interval="4000" type="card" height="400px">
        <el-carousel-item v-for="item in banners " :key="item">
            <img :src="item" alt="" @click="jump">
        </el-carousel-item>
    </el-carousel>
    <div class="body-box" v-for="(tmp,key) in bodyList" :key="key">
        <h2 class="box-title">热播剧</h2>
        <h3 class="box-title2">猜你喜欢</h3>
        <ul class="box-f1">
            <li v-for="(i,k) in tmp.leftList" :key="k" @mouseenter="onmouse(k,key)" @mouseleave="mouseout(k,key)">                
                <a href="javascript:;" @click="jump">
                    <img :src="'http://127.0.0.1:3000/test2/img/'+i.imgUrl" alt="图片删除">
                    <div class="mask" v-show="i.isHover">
                        <p class="title">{{i.text}} </p>
                        <p class="bsum">播放:{{i.playNumber}} w</p>
                    </div>
                </a>
            </li>        
        </ul>
        <ul class="box-f1-right">
            <li class="item" :data-i="k" v-for="(i,k) in tmp.rightList" :key="k"  @mouseenter="itemScroll(k,key)"><p>{{i}}</p></li>
            <li class="box">
                <div class="lbox" :style="{ marginTop : tmp.marginTop + 'px' }">
                    <div v-for="(i,k) in tmp.imgList" :key="k" :class='"d"+(k+1)'>
                        <img :src="'http://127.0.0.1:3000/test2/img/'+i" alt="图片删除" @click="jump">
                    </div>
                </div>
            </li>
        </ul>
    </div>
    <div style="height:300px" title="占位"></div>
    <infinite-loading spinner="bubbles" @infinite="infiniteHandler" ref="infiniteLoading"></infinite-loading>
</div>
</template>

<script>

import InfiniteLoading from 'vue-infinite-loading';
const url = "http://127.0.0.1:3000/content"
export default {
components:{ InfiniteLoading },
    data(){
        return{
            page: 1,
            bodyList:[],
            banners : [
            "http://127.0.0.1:3000/test2/img/banner1.png", 
            "http://127.0.0.1:3000/test2/img/banner2.png", 
            "http://127.0.0.1:3000/test2/img/banner3.png", 
            "http://127.0.0.1:3000/test2/img/banner4.png", 
            ]
        }
    },
    beforeCreate(){
        this.axios.get(url).then(result=>{      
        this.bodyList.push(result.data)       
        })
    },
    methods:{
        jump(){
            this.$router.push("/video")
        },
        onmouse(k,key){
            this.bodyList[key].leftList[k].isHover = true;
        },
        mouseout(k,key){            
            this.bodyList[key].leftList[k].isHover = false;
        },
        itemScroll(k,key){            
            this.bodyList[key].marginTop = -k * 190            
        },
        infiniteHandler($state) {

        setTimeout(() => {

        const temp = [];

        for (let i = this.bodyList.length + 1; i <= this.bodyList.length + 1; i++) {

            this.axios.get(url).then(result=>{      
            this.bodyList.push(result.data)       
            })

        }

        $state.loaded();

        this.bodyList.length >= 5 && $state.complete();

        }, 500);

        }
    }
}
</script>

<style lang="scss" scoped="" type="text/css">
$w:1530px;
$b:#ddd;
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
    ul{
        list-style: none;
    }
}
.banner{
    width: $w;
    margin: 0 auto;
    position: relative;
    left: 50px;
    padding-top: 18px;
}
.el-carousel__item img {
    width: auto;
    height: 100%;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
.body{    
    .body-nav-right{
        float: right;
        width:40%;
        li{
            transition: 0.5s;
            a{
                font-size: 14px;
                color: #666;            
                text-decoration: none;
                cursor: pointer;
            }
            width: 25%;
            float: left;
            height: 120px;
            img{
                display: block;
                margin:10px 38px;
                width: 50%;
            }
            span{
                font-weight:700;                display: block;
                width:100%;
                text-align: center;
            }
        }
        li:hover{
            background: #444;
            border-radius:5px;
            span{
                font-weight:700;                display: block;
                width:100%;
                text-align: center;
                color: #ddd;
            }
        }
    }
    width: $w;
    margin:0px auto;
    padding: 5px 0;
    background:$b;
    height: 120px;
    .body-nav{
        width:12%;
        height:120px;
        float: left;
        padding:5px;
        li>a{
            font-size: 14px;
            color: #666;            
            text-decoration: none;
            cursor: pointer;
        }
    }
}
.body-box{
    background: rgba(84, 92, 100,.8);
    width: $w;
    height:235px;
    margin:0 auto;
    left: 50px;
    position: relative;
    color: #fff;
    border-radius:10px;
    .box-title{
        display: block;
        width:100px;
        text-align: left;
        margin-left: 10px;
    }
    .box-title2{
        position: absolute;
        right: 220px;
        top: 5px;
    }
    .box-f1{
        width:80%;
        height: 200px;
        margin: 0;
        li{
            position: relative;
            width: 24%;
            height:190px;
            margin:5px;
            float: left;
            overflow: hidden;
    border-radius:5px;
            a{
                display: block;
                width:100%;
                height: 100%;
                .mask{
                    width:100%;
                    height: 100%;
                    background: rgba(0, 0, 0, 0.5);
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 1;
                    .title{
                        margin:5px;
                        color:#fff;
                        height: 79%;
                        overflow: hidden;
                        -webkit-line-clamp:7;
                        -webkit-box-orient:vertical;
                        display: -webkit-box;
                    }
                    .bsum{
                        color: #ddd;
                        font-size: 16px;
                        float:right;
                        margin-right:5px 
                    }
                }
                img{
                    width: 306px;
                    height:auto;
                }
            }
            
        }
    }
    .box-f1-right{
        display: block;
        float: right;
        position: relative;
        margin-right: 5px;
        top:-195px;
        width: 20%;
        height:190px;
        font-weight: 300;
        font-family: "华文中宋";
        h4{
            background:#ddd;
            padding:5px;
        }
        li{
            width: 120px;
            height: 31.6px;
            background:#000;
            color:#fff;
            font-size: 20px;
            transition:.5s;
            padding: 5px;
        }
        li:hover{
            background:rgb(182, 179, 179);
            color:rgb(51, 51, 51);
            font-size: 20px;
        }
        .box{
            padding: 0;
            width: 186px;
            height: 190px;
            position: absolute;
            right: 0;
            top:0px;
            overflow: hidden;
            .lbox{
                width: 186px;
                height: 190*6px;
                margin-top: 0;
                transition: .5s;
                div{
                    height: 190px;
                    width: 186px;
                    overflow: hidden;
                }
                img{
                    width: 100%;
                    height: auto;
                }
            }
        }
        .box:hover{
            margin: 0;
            padding: 0;
        }
    }    
}
</style>


