<template>
    <div class="head-app"> 
      <el-row>
        <el-col :span="24">          
          <el-menu
            default-active="1"
            class="el-menu-demo myNav"
            mode="horizontal"
            @select="handleSelect"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b">
            <el-menu-item index="1" @click="jump('')">首页</el-menu-item>
            <el-submenu v-for="(item,key) in navList" :key="key" :index="key">
              <template slot="title">{{item.title}}</template>
              <el-menu-item :index="key+'-'+k" v-for="(i,k) in 3" :key="k">{{item.content[k]}}</el-menu-item>
            </el-submenu>
            
            <a href="javascript:;" 
            class="myicon" 
            v-for="(i,k) in iconList" 
            :key="k" 
            @click="jump(i.link)">
              <i :class="i.class"></i>
              <p class="iname">{{i.text}}</p>
            </a>
          </el-menu>
        </el-col>  
      </el-row>      
    </div>
</template>

<script>
  export default {
    data() {
      return {
        navList:[
          {title:"电视剧",content:["都挺好","知否","国家宝藏"]},
          {title:"电影",content:["妇联4","绿皮书","白蛇-缘起"]},
          {title:"动漫",content:["一拳超人2","进击的巨人3","辉夜姬大人"]},
        ],
        iconList:[{class:"el-icon-star-off",text:"收藏",link:"login"},
                  {class:"el-icon-time"    ,text:"历史",link:"login"},
                  {class:"el-icon-more"    ,text:"登录",link:"login"}]
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      jump(link){
        this.$router.push("/"+link)
      }
    }
  }
</script>

<style>
.iname{
  font-size: 12px;
  padding: 0;margin: 0;
}
.myicon{
  color: #fff;
  font-size: 24px;
  width: 30px;height: 30px;
  margin: 2px 10px;padding: 3px 10px 23px;
  float: right;
}
.head-app{
  position: fixed;
  top:0;
  z-index: 5;
  left: 50%;
  margin-left: -765px;
}
.myNav{
  margin: 0 auto ;
  width:1530px;
  top: 0;
  position: relative;
  left: 50px;
}
.el-menu.el-menu--horizontal{
  border: 1px solid rgb(84, 92, 100)
}
#user-item{float: left;}
</style>



